.main {
    display: flex;
    justify-content: center;
    align-items: center;
}

.underoval {
    position: relative;
    top: 250px;
}

/* The animation code */
@keyframes spin {
    0%   {transform: rotate(0deg);}
    25%  {transform: rotate(90deg); top: 150px;}
    50%  {transform: rotate(180deg); top: 50px}
    75%  {transform: rotate(270deg); top: 150px}
    100% {transform: rotate(360deg);}
}

.oval {
    width: 50px;
    height: 160px;
    background: #ff9d4d;
    border-radius: 50%;
    z-index: 10;
    position: relative;
    top: 250px;
    /* left: 50%; */
    animation-name: spin;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.line {
    width: 3px;
    height: 30px;
    background: #000000;
    position: absolute;
    left: 23px;
    transform: rotate(70deg);
}

.visually-hidden {
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
}

.phoneWidth {
    width: 90vw;
    max-width: 400px;
}